.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
h1{
  margin-top: 3%;
}

button {
  align-items: center;
  background-color: #fee6e3;
  border: 2px solid #111;
  border-radius: 10px;
  box-sizing: border-box;
  color: #111;
  cursor: pointer;
  font-family: Inter,sans-serif;
  font-size: 16px;
  height: 48px;
  justify-content: center;
  line-height: 24px;
  max-width: 100%;
  padding: 0 25px;
  position: relative;
  text-align: center;
  text-decoration: none;
}
table{
  align-self: center;
  margin-bottom: 4opx;
}
table td{
  padding: 10px 19px;
}
p{
  text-align: center;
  padding-bottom: 30px;
  font-size: 18px;
}
#explain{
  margin-bottom: 20px;
  margin-top: 40px;
  margin-left: 20%;
  margin-right: 20%;
  font-size: 16px;
}

.square {
  height: 350px;
  margin: auto;
  display: flex;
  justify-content:center;
  width: 90%;
  position: relative;
  align-items: center;
}
.vertical-center{
  position: relative;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  align-items: center;
}

.song{
  margin: auto;
  font-size: 20px;
  margin-left: 35%;
  margin-right: 35%;
  text-align:center;
}